.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

/** User Interface **/
.light {
  color: #313242;
  background: #f3f3f4; }

.dark {
  color: #ced4de;
  background: #3c4149; }

.form-item {
  padding: 5px 10px; }
  .form-item-x2 {
    padding: 10px 10px; }
  .form-item-x3 {
    padding: 15px 10px; }

.button {
  line-height: 30px;
  height: 30px;
  font-size: 13px;
  display: inline-block;
  color: #ced4de;
  background: #3c4149;
  border-radius: 2px;
  padding: 0 15px;
  box-sizing: border-box;
  text-decoration: none;
  text-align: center;
  border: none;
  outline: none;
  font-weight: normal; }
  .button:hover {
    color: #fcfcfd;
    background: #484d57; }
  .button:active {
    color: white;
    background: #30353b; }
  .button-main {
    color: #fff;
    background: #e74c3c; }
    .button-main:hover {
      color: white;
      background: #ea6153; }
    .button-main:active {
      color: white;
      background: #e43725; }

.button-group {
  display: inline-block; }
  .button-group:after {
    display: table;
    width: 100%;
    content: '';
    clear: both; }
  .button-group .button {
    display: block;
    border-radius: 0;
    float: left; }
    .button-group .button:first-child {
      border-top-left-radius: 2px;
      border-bottom-left-radius: 2px; }
    .button-group .button:last-child {
      border-top-right-radius: 2px;
      border-bottom-right-radius: 2px; }

input[type=text],
input[type=password],
textarea {
  line-height: 30px;
  height: 30px;
  font-size: 13px;
  display: inline-block;
  color: #ced4de;
  background: #3c4149;
  padding: 0 15px;
  box-sizing: border-box;
  border: none;
  border-radius: 2px;
  outline: none; }
  input[type=text]:focus,
  input[type=password]:focus,
  textarea:focus {
    color: #fcfcfd;
    background: #484d57;
    transition: color .2s ease; }

.label {
  padding: 5px 15px;
  display: inline-block;
  font-size: 11px;
  line-height: 16px;
  border-radius: 2px; }
  .label-error {
    background: #e74c3c;
    color: #fff; }

.pagination {
  line-height: 30px;
  height: 30px;
  font-size: 13px;
  display: inline-block;
  color: #ced4de;
  background: #3c4149;
  display: block;
  float: left;
  margin: 0;
  padding: 0;
  border-radius: 2px; }
  .pagination ul,
  .pagination li {
    list-style: none; }
  .pagination li {
    display: block;
    float: left;
    width: 30px;
    position: relative;
    z-index: 1;
    overflow: hidden; }
    .pagination li:first-child {
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px; }
    .pagination li:last-child {
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px; }
    .pagination li span,
    .pagination li a {
      font-weight: normal;
      display: block;
      width: 30px;
      text-align: center;
      text-decoration: none;
      color: #b4bac6; }
    .pagination li a:hover {
      color: #fcfcfd;
      background: #484d57; }
    .pagination li a:focus {
      color: white;
      background: #30353b; }
    .pagination li.disabled {
      cursor: default; }
      .pagination li.disabled span {
        color: #fcfcfd;
        background: #484d57;
        color: #6a7381; }
    .pagination li.active {
      z-index: 2; }
      .pagination li.active span {
        background: #e74c3c;
        color: #fff;
        font-weight: bold; }

header {
  color: #ced4de;
  background: #3c4149;
  background: #474d57;
  height: 42px;
  line-height: 42px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9; }
  header .logo {
    display: block;
    width: 200px;
    text-decoration: none;
    background: rgba(255, 255, 255, 0);
    height: 42px;
    overflow: hidden; }
    header .logo:hover {
      transition: background .2s ease;
      background: rgba(255, 255, 255, 0.1); }
    header .logo img {
      width: 22px;
      height: 22px;
      margin: 10px 15px 10px 10px;
      display: block;
      float: left; }
    header .logo h1 {
      color: #f6f6f6;
      display: block;
      float: left;
      line-height: 42px;
      height: 42px;
      font-size: 18px;
      margin: 0;
      padding: 0;
      font-weight: normal; }

aside.nav {
  color: #ced4de;
  background: #3c4149;
  width: 200px;
  padding-top: 42px;
  height: 100%;
  position: fixed;
  z-index: 8;
  left: 0;
  top: 0;
  box-sizing: border-box; }
  aside.nav nav {
    padding: 10px 15px; }
    aside.nav nav h4 {
      text-transform: uppercase;
      font-size: 12px; }
    aside.nav nav a {
      color: #ced4de;
      background: #3c4149;
      display: block;
      width: 100%;
      box-sizing: border-box;
      line-height: 28px;
      height: 28px;
      padding: 0 10px;
      text-decoration: none;
      border-radius: 2px; }
      aside.nav nav a .fa {
        display: block;
        float: left;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin: 4px 8px 4px -5px;
        font-size: 9px;
        line-height: 20px;
        text-align: center;
        box-shadow: inset 0 0 0 1px #b4bac6; }
      aside.nav nav a:hover {
        color: #fcfcfd;
        background: #484d57;
        color: #fff; }
        aside.nav nav a:hover .fa {
          box-shadow: inset 0 0 0 1px #fff; }

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  z-index: 7;
  box-sizing: border-box;
  padding: 10px 25px;
  padding-left: 225px;
  padding-top: 52px; }
  .content h2 {
    font-size: 24px;
    margin: 5px 0 20px 0; }
  .content table {
    width: 100%;
    box-shadow: 0 0 0 1px #b4bac6;
    border-radius: 2px; }
    .content table td {
      padding: 15px 10px; }
    .content table tfoot td,
    .content table thead td {
      background: rgba(0, 0, 0, 0.05);
      color: #5a6779;
      font-weight: bold;
      padding: 10px 10px;
      border-right: #b4bac6 1px solid;
      border-bottom: #b4bac6 1px solid; }
      .content table tfoot td:last-child,
      .content table thead td:last-child {
        border-right: none; }
    .content table tfoot .controls,
    .content table thead .controls {
      border-bottom: #b4bac6 1px solid; }
    .content table tbody tr td {
      border-bottom: rgba(180, 186, 198, 0.2) 1px solid; }
      .content table tbody tr td.controls {
        width: 82px; }
    .content table tbody tr:nth-child(2n) td {
      background: rgba(255, 255, 255, 0.5); }
    .content table tbody tr:last-child td {
      border-bottom: none; }
    .content table tfoot td {
      font-weight: normal;
      border-bottom: none;
      border-top: #b4bac6 1px solid; }
      .content table tfoot td:after {
        display: table;
        width: 100%;
        content: '';
        clear: both; }
      .content table tfoot td .items-count {
        float: right; }
    .content table figure {
      background: #fff;
      border-radius: 3px;
      display: block;
      margin: -10px 0;
      box-shadow: 0 0 0 1px #e74c3c; }
      .content table figure img {
        width: inherit;
        height: inherit;
        display: block;
        border-radius: 3px; }
    .content table .label {
      line-height: 30px;
      height: 30px;
      font-size: 13px;
      display: inline-block;
      padding: 0 10px;
      display: block;
      float: left; }

/** Pages Customization **/
.auth {
  width: 260px;
  height: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -100px 0 0 -130px; }
  .auth h2 {
    text-align: center;
    display: block;
    width: 100%; }
  .auth input {
    width: 100%; }
  .auth .button {
    display: block;
    width: 80px;
    margin: 0 auto; }

html, body {
  background: #f3f3f4;
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
  color: #313242; }
